$parentClassName: expertises;

.#{$parentClassName} {
	width: 100%;
	display: flex;
	justify-content: center;
	.#{$parentClassName}__container {
		margin: {
			top: 75px;
			bottom: 25px;
		}
		width: 80%;
		.#{$parentClassName}__inner {
			width: 100%;
			height: 100%;
			.#{$parentClassName}__description {
				text: {
					transform: uppercase;
					-webkit-transform: uppercase;
					-moz-transform: uppercase;
					-ms-transform: uppercase;
					-o-transform: uppercase;
				}
				font: {
					family: "Inter";
					size: 14px;
					weight: 600;
				}
				letter: {
					spacing: 1px;
				}
				line: {
					height: 20px;
				}
				color: white;
			}
			.#{$parentClassName}__list {
				width: 100%;
				height: 850px;
				margin: 50px 0;
				display: grid;
				grid-template-columns: repeat(5, 1fr);
				grid-template-rows: repeat(4, 1fr);
				grid-gap: 25px;
			}
		}
	}
}
